.backdrop {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0,0,0,0.3);
    padding: 10%
}

.modal {
    background-color: #fff;
    border-radius: 5px;
    max-width: 500px;
    margin: 0 auto;
    padding: 30px
}

.modalText {
    min-height: 100px;
    display: flex;
    align-items: center;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.exam-wrapper, .result-wrapper {
  background: url(/examination-module/static/media/background-default.6790fe66.jpg) center/cover repeat;
}

.titles {
  text-align: center;
  padding: 2%
}

.questions {
  padding: 0 5% 3%;
}

.question {
  position: relative;
  margin-top: 1%;
  border: 1px solid #2196F3;
  padding: 3%;
  background: #ffffff;
  border-radius: 5px;
}

.question h2 {
  width: 90%;
}

.buttons {
  display: flex;
  justify-content: center;
  margin-top: 3%;
}

.btn {
  border: none;
  border-radius: 5px;
  color: white;
  padding: 14px 28px;
  font-size: 0.9rem;
  cursor: pointer;
  background-color: #2196F3;
  margin-right: 3%;
}

.btn:hover {background: #0b7dda;}

.btn-inline {
  margin-right: 9%;
  display: inline-block;
  width: 25%;
}

.btn-inline:last-child {margin-right: 0;}

.btn-footer {
  text-align: center;
}

.btn-result {
  margin-top: 2%;
}

.question-body {
  padding-top: 2%;
}

.container, .container-checkbox {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

input[type="text"] {
  width: 20%;
  padding: 8px 10px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  resize: none
}

.question-title {
  display: inline-block;
  padding-bottom: 0.5rem;
}

.result-wrapper {
  height: 100vh;
}

.result-window {
  max-width: 500px;
  margin: auto;
  background: #ffffff;
  border-radius: 5px;
}

.result-window h3 {
  font-size: 1.5rem;
  padding: 1rem 0;
}

.result-item {
  margin-bottom: 12px;
  font-size: 1.2rem;
}

.result-item:nth-child(4) {
  padding-bottom: 2rem;
}

.result-section {
  padding: 1% 3% 3%;
}

.right-answer * {
  display: inline-block;
  vertical-align: middle;
  margin-right: 1rem
}

.right-answer {
  position: absolute;
  right: 1rem;
  top: 1.5rem;
}

.img-right {
  max-height: 3rem;
  max-width: 3rem;
}

.right-title {
  font-weight: bold;
  font-size: 1rem;
  color: #008000;
}

.green-border {
    border-color: #0080007d;
}

.green-blue {
    border-color: #2196F3;
}

/*Reset*/
html, body, div, section, article, p, blockquote, ul, li, span{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Calibri', sans-serif;
}

a {
    text-decoration: none;
    cursor: pointer;
}

img {
    border: 0;
}

button {
    cursor: pointer;
    outline: none;
}

h1, h2, h3, h4, h5, p {
    color: #000000;
    margin: 0;
}

h1 {
    font-weight: 700;
    line-height: 1.08;
}

h2 {
    font-weight: 700;
    line-height: 1.08;
}

ul {
    list-style-type: none;
}

/* Hide the browser's default radio button */
.container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
}

/* Create a custom radio button */
.check-mark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
    border-radius: 50%;
}

/* On mouse-over, add a grey background color */
.container:hover input ~ .check-mark {
    background-color: #ccc;
}

/* When the radio button is checked, add a blue background */
.container input:checked ~ .check-mark {
    background-color: #2196F3;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.check-mark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the indicator (dot/circle) when checked */
.container input:checked ~ .check-mark:after {
    display: block;
}

/* Style the indicator (dot/circle) */
.container .check-mark:after {
    top: 9px;
    left: 9px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: white;
}
/* Hide the browser's default checkbox */
.container-checkbox input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */
.check-mark-checkbox {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
}

/* On mouse-over, add a grey background color */
.container-checkbox:hover input ~ .check-mark-checkbox {
    background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.container-checkbox input:checked ~ .check-mark-checkbox {
    background-color: #2196F3;
}

/* Create the check-mark-checkbox/indicator (hidden when not checked) */
.check-mark-checkbox:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the check-mark-checkbox when checked */
.container-checkbox input:checked ~ .check-mark-checkbox:after {
    display: block;
}

/* Style the check-mark-checkbox/indicator */
.container-checkbox .check-mark-checkbox:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
.select-style {
    border: 1px solid #ccc;
    width: 20%;
    margin: 0.6rem 0;
    border-radius: 3px;
    overflow: hidden;
    background: url(data:image/png;base64,) no-repeat 95% 50%;
    background-size: 5%;
}

.select-style select {
    padding: 5px 8px;
    width: 100%;
    height: 1.84rem;
    border: none;
    box-shadow: none;
    background: transparent no-repeat none;
    -webkit-appearance: none;
}

.select-style select:focus {
    outline: none;
}
